   border-radius: 10px;
   width:200px;
   height:150px;
   justify-content: center;
   align-items: center;
   background-color: black;
   overflow:hidden;

}
.experience>p{
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size:16px;
    letter-spacing:1px;
}
